import React, { useState } from "react";
import PropTypes from "prop-types";
import classNames from "classnames";

import { useTranslation } from "react-i18next";
import { List, message } from "antd";
import Button from "../../UI/Button/Button";
import {
  ACCEPT_TOKEN_CONVERSION_RATIO,
  DURATIONS,
  NFTS,
  NFT_PATTERNS,
  toPattern,
} from "../../../utils/utils";
import {
  useDefaultInviterCode,
  useIsRegistered,
  useRegisteredInvitationCodeContains,
} from "../../../hooks/useInvitation";
import { useQuery } from "../../../hooks/useQuery";
import { useBuy } from "../../../hooks/useManager";
import { useWallet } from "../../../hooks/useWallet";
import { TOKENS, MANAGER_ADDRESS } from "../../../constants/addresses";
import {
  useTokenAllowance,
  useTokenApprove,
  useTokenBalance,
} from "../../../hooks/useToken";

import styles from "./MiningNFTAction.module.scss";
import commonStyles from "../../../common.module.scss";

const MiningNFTAction = (props) => {
  const { className } = props;
  const { t } = useTranslation();
  const [params, setParams] = useState({ value: null, duration: null });
  const { data: defaultInviterCode } = useDefaultInviterCode();
  const { data: isRegistered } = useIsRegistered();
  const { state: buyState, buy } = useBuy();
  const { state: approveState, approve } = useTokenApprove(TOKENS.ACCEPT);
  const { account } = useWallet();
  const { isValidInvitation } = useRegisteredInvitationCodeContains();
  const queryInviterCode = useQuery()?.get("referral");
  const { data: nativeBalance } = useTokenBalance(TOKENS.NATIVE);
  const { data: acceptBalance } = useTokenBalance(TOKENS.ACCEPT);
  const { data: acceptTokenAllowance } = useTokenAllowance(
    TOKENS.ACCEPT,
    MANAGER_ADDRESS
  );

  const setValue = (value) => {
    setParams((prev) => ({ ...prev, value }));
  };

  const setDuration = (duration) => {
    setParams((prev) => ({ ...prev, duration }));
  };

  const approveHandler = async () => {
    if (approveState?.isLoading) {
      return;
    }
    if (!account) {
      message.info("Please connect to wallet");
      return;
    }

    // check for gas
    if (nativeBalance < 0.01) {
      message.info("Need gas");
      return;
    }
    approve(MANAGER_ADDRESS);
  };

  const buyHandler = async () => {
    if (buyState?.isLoading) {
      return;
    }
    if (!account) {
      message.info("Please connect to wallet");
      return;
    }
    // not ready
    if (!(defaultInviterCode && isRegistered !== null)) {
      return;
    }
    // check params
    if (!params.value) {
      message.info("Please select an NFT");
      return;
    }
    if (!params.duration) {
      message.info("Please select a duration");
      return;
    }
    // get pattern
    const pattern = toPattern(params.value, params.duration);
    if (pattern === NFT_PATTERNS.PATTERN_UNKNOWN.PATTERN) {
      return;
    }
    // check for gas
    if (nativeBalance < 0.01) {
      message.info("Need gas");
      return;
    }
    // check for accept token balance
    if (acceptBalance < params.value * ACCEPT_TOKEN_CONVERSION_RATIO) {
      console.log(acceptBalance, params.value);
      message.info(`Your USDT balance < ${params.value}`);
      return;
    }
    // handle invitation code
    let invitationCode = defaultInviterCode;
    if (isRegistered) {
      invitationCode = "";
    } else if (queryInviterCode) {
      // handle invitationCode from query param
      if (!(await isValidInvitation(queryInviterCode))) {
        message.info("Invalid referral code");
        return;
      }
      invitationCode = queryInviterCode;
    }
    buy([pattern], invitationCode);
  };

  return (
    <div className={classNames(styles.MiningNFTAction, className)}>
      <h2 className={commonStyles.gradientTitle}>{t("pick-a-nft-to-start")}</h2>
      <div className={styles.nftSelectorWrap}>
        <p className={classNames(commonStyles.mainText, styles.subtitle)}>
          {t("type-of-nfts")}
        </p>
        <List
          className={styles.selectorList}
          grid={{
            gutter: 16,
            xs: 4,
            sm: 4,
            md: 4,
            lg: 4,
            xl: 4,
            xxl: 4,
          }}
          dataSource={Object.values(NFTS)}
          renderItem={(item) => (
            <List.Item
              onClick={() => {
                setValue(item.value);
              }}
            >
              <div className={commonStyles.square}>
                <div
                  className={classNames(styles.nftSelectorListItem, {
                    [styles.active]: params.value === item.value,
                  })}
                >
                  <img
                    src={`/static/images/nfts/${item.id}.gif`}
                    alt="support"
                  />
                </div>
              </div>
              <p className={styles.nftPrice}>{item.value} USDT</p>
            </List.Item>
          )}
        />
      </div>
      <div className={styles.durationSelectorWrap}>
        <p className={classNames(commonStyles.mainText, styles.subtitle)}>
          {t("mining-duration")}
        </p>
        <List
          className={styles.selectorList}
          grid={{
            gutter: 16,
            xs: 4,
            sm: 4,
            md: 4,
            lg: 4,
            xl: 4,
            xxl: 4,
          }}
          dataSource={DURATIONS}
          renderItem={(item) => (
            <List.Item
              onClick={() => {
                setDuration(item.duration);
              }}
            >
              <div
                className={classNames(styles.durationSelectorListItem, {
                  [styles.active]: params.duration === item.duration,
                })}
              >
                {item.duration} {t(item.unitText)}
              </div>
            </List.Item>
          )}
        />
      </div>
      {acceptTokenAllowance ? (
        <Button gradient onClick={buyHandler} loading={buyState?.isLoading}>
          {t("start-mining")}
        </Button>
      ) : (
        <Button
          gradient
          onClick={approveHandler}
          loading={approveState?.isLoading}
        >
          {t("Approve")} USDT
          {/* Approve USDT */}
        </Button>
      )}
    </div>
  );
};

MiningNFTAction.propTypes = { className: PropTypes.string };
MiningNFTAction.defaultProps = { className: "" };

export default MiningNFTAction;
