import React from "react";
import { message } from "antd";
import { useTranslation } from "react-i18next";
import CopyToClipboard from "react-copy-to-clipboard";
import classNames from "classnames";

import { useWallet } from "../../../hooks/useWallet";
import {
  useInviterRecords,
  useIsRegistered,
} from "../../../hooks/useInvitation";

import Button from "../../UI/Button/Button";
import { useRecord, useTakeBonus } from "../../../hooks/useManager";

import styles from "./MiningReferral.module.scss";
import commonStyles from "../../../common.module.scss";
import { formatOutput } from "../../../utils/utils";
import { useTokenBalance } from "../../../hooks/useToken";
import { TOKENS } from "../../../constants/addresses";

const MiningReferral = () => {
  const { data: inviterRecord } = useInviterRecords();
  const { data: isRegistered } = useIsRegistered();
  const { data: managerRecord } = useRecord();
  const { state: takeBonusState, takeBonus } = useTakeBonus();
  const { t } = useTranslation();
  const { data: nativeBalance } = useTokenBalance(TOKENS.NATIVE);
  const { account } = useWallet();

  const referralLink = `${window.location.origin}/#/mining?referral=${inviterRecord?.invitationCode}`;

  const { inviteeAmounts } = inviterRecord;
  const firstLayerInvitees = inviteeAmounts?.[0]?.value;

  const sliceEndIndex = Math.max(firstLayerInvitees, 0);

  const takeBonusHandler = () => {
    if (takeBonusState.isLoading) {
      return;
    }
    if (!account) {
      message.info("Please connect to wallet");
      return;
    }
    if (!managerRecord?.bonusAmount) {
      message.info("Nothing to claim");
      return;
    }
    // check for gas
    if (nativeBalance < 0.01) {
      message.info("Need gas");
      return;
    }
    takeBonus();
  };

  return (
    <div className={styles.MiningProfile}>
      <div className={styles.header}>
        <h2 className={commonStyles.gradientTitle}>{t("Referrals")}</h2>
      </div>
      {isRegistered && inviteeAmounts ? (
        <>
          <div className={styles.referralLinkWrap}>
            <div className={styles.referralLink}>{referralLink}</div>

            <CopyToClipboard
              text={referralLink}
              onCopy={() => {
                message.success(t("referral-link-copied"));
              }}
            >
              <Button className={styles.button}>{t("Copy")}</Button>
            </CopyToClipboard>
          </div>
          <div className={styles.stats}>
            <div className={styles.statsItem}>
              <h2 className={classNames(commonStyles.mainText)}>
                {t("Total Referrals")}
              </h2>
              <p>
                {formatOutput(firstLayerInvitees)}
                {/* <span className={classNames(commonStyles.mainText, styles.unit)}>
              USDT
            </span> */}
              </p>
            </div>
            <div className={styles.statsItem}>
              <h2 className={classNames(commonStyles.mainText)}>
                {/* {t("total-rewards")} */}
                {t("Total Bonus")}
              </h2>
              <p>
                {formatOutput(managerRecord?.bonusAmount)}
                <span
                  className={classNames(commonStyles.mainText, styles.unit)}
                >
                  USDT
                </span>
              </p>
              <Button
                className={styles.button}
                gradient
                loading={takeBonusState.isLoading}
                onClick={takeBonusHandler}
                disabled={!managerRecord?.bonusAmount}
              >
                {t("Claim")}
              </Button>
            </div>
          </div>

          {sliceEndIndex && (
            <>
              <hr />
              <div className={styles.referralList}>
                {inviteeAmounts?.slice(0, sliceEndIndex).map((item, index) => (
                  <div className={styles.referralListItem} key={item.id}>
                    <p className={styles.ordinal}>
                      {t("Tier")} {index + 1}
                    </p>
                    <p className={styles.reward}>{item.value}</p>
                    <p className={styles.reward}>
                      {managerRecord?.inviteeCapitalAmounts[index]} USDT
                    </p>
                  </div>
                ))}
              </div>
            </>
          )}
        </>
      ) : (
        <h2 className={styles.note}>
          Please start mining to enable referral system
        </h2>
      )}
    </div>
  );
};

MiningReferral.propTypes = {};

export default MiningReferral;
