import React from "react";
import PropTypes from "prop-types";
import classNames from "classnames";

import { useTranslation } from "react-i18next";
import { truncAddress } from "../../../utils/utils";

import styles from "./MiningLeaderboard.module.scss";
import commonStyles from "../../../common.module.scss";
import { useRanks } from "../../../hooks/useManager";

const MiningLeaderboard = (props) => {
  const { className } = props;
  const { t } = useTranslation();
  const { data: rankRecords } = useRanks();

  return (
    <div className={classNames(styles.MiningLeaderboard, className)}>
      <div className={styles.titleWrap}>
        <img src="/static/images/mining/trophy.png" alt="trophy" />
        <h2 className={commonStyles.gradientTitle}>Top 10</h2>
      </div>
      <div className={styles.listWrap}>
        <div className={styles.listTitle}>
          <p>{t("Rank")}</p>
          <p>{t("Direct Referrals")}</p>
          {/* <p>{t("Rewards")}</p> */}
        </div>
        <div className={styles.list}>
          {rankRecords.map((item, index) => (
            <div className={styles.listItem} key={`rank${index + 1}`}>
              <div>
                <p className={styles.ordinal}>{index + 1}</p>
                <p className={styles.address}>{truncAddress(item.who)}</p>
              </div>
              <div className={styles.reward}>
                {item.firstLayerInviteeCapitalAmount} USDT
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

MiningLeaderboard.propTypes = { className: PropTypes.string };
MiningLeaderboard.defaultProps = { className: "" };

export default MiningLeaderboard;
