import React from "react";
import classNames from "classnames";
import { List, message } from "antd";
import { useTranslation } from "react-i18next";
import MiningProfileListItem from "../MiningProfileListItem/MiningProfileListItem";
import { useNFTRecords } from "../../../hooks/useNFT";
import { useClaim, useRefund } from "../../../hooks/useManager";
import { useTokenBalance } from "../../../hooks/useToken";
import { TOKENS } from "../../../constants/addresses";

import styles from "./MiningProfile.module.scss";
import commonStyles from "../../../common.module.scss";
import { useWallet } from "../../../hooks/useWallet";
import { checkTime } from "../../../utils/utils";
import Button from "../../UI/Button/Button";

const MiningProfile = () => {
  const { t } = useTranslation();
  const { data: nftRecords } = useNFTRecords();
  const { state: claimState, claim } = useClaim();
  const { state: refundState, refund } = useRefund();
  const { data: nativeBalance } = useTokenBalance(TOKENS.NATIVE);
  const { account } = useWallet();

  const totalValue = nftRecords.reduce((accum, item) => accum + item.value, 0);
  const totalClaimable = nftRecords.reduce(
    (accum, item) => accum + item.rewardAmount,
    0
  );

  const isClaimable = (tokenRecord) => tokenRecord.rewardAmount > 0;
  const isRefundable = (tokenRecord) =>
    checkTime(tokenRecord.unlockTimestamp * 1000) &&
    tokenRecord.rewardAmount === 0;

  const claimHandler = (tokenIds) => {
    if (claimState.isLoading) {
      return;
    }
    if (!account) {
      message.info("Please connect to wallet");
      return;
    }
    // find and verify claimable token ids
    let claimableTokenIds = nftRecords
      .filter((record) => isClaimable(record))
      .map((record) => record.id);

    if (tokenIds?.length) {
      claimableTokenIds = claimableTokenIds.filter((record) =>
        tokenIds.some((id) => id === record)
      );
    }

    if (!claimableTokenIds?.length) {
      message.info("Nothing to claim");
      return;
    }

    // check for gas
    if (nativeBalance < 0.01) {
      message.info("Need gas");
      return;
    }

    claim(claimableTokenIds);
  };

  const refundHandler = (tokenIds) => {
    if (refundState.isLoading) {
      return;
    }
    if (!account) {
      message.info("Please connect to wallet");
      return;
    }
    // find and verify refundable token ids
    let refundableTokenIds = nftRecords
      .filter((record) => isRefundable(record))
      .map((record) => record.id);

    if (tokenIds?.length) {
      refundableTokenIds = refundableTokenIds.filter((record) =>
        tokenIds.some((id) => id === record)
      );
    }
    if (!refundableTokenIds?.length) {
      message.info("NFT is locked or rewards haven't been claimed.");
      return;
    }

    // check for gas
    if (nativeBalance < 0.01) {
      message.info("Need gas");
      return;
    }

    refund(refundableTokenIds);
  };

  return (
    <div className={styles.MiningProfile}>
      <div className={styles.header}>
        <h2 className={commonStyles.gradientTitle}>{t("my-mining")}</h2>
      </div>
      <div className={styles.stats}>
        <div className={styles.statsItem}>
          <h2 className={classNames(commonStyles.mainText)}>
            {t("Total NFT Value")}
          </h2>
          <p>
            {totalValue}
            <span className={classNames(commonStyles.mainText, styles.unit)}>
              USDT
            </span>
          </p>
        </div>
        <div className={styles.statsItem}>
          <h2 className={classNames(commonStyles.mainText)}>
            {t("Total Claimable")}
          </h2>
          <p>
            {totalClaimable}
            <span className={classNames(commonStyles.mainText, styles.unit)}>
              USDT
            </span>
          </p>
          <Button
            className={styles.button}
            gradient
            loading={claimState.isLoading}
            disabled={!totalClaimable}
            onClick={claimHandler}
          >
            {t("Claim")}
          </Button>
        </div>
        <div className={styles.statsItem}>
          <h2 className={classNames(commonStyles.mainText)}>{t("My NFTs")}</h2>
          <p>{nftRecords.length}</p>
        </div>
      </div>
      <hr />
      <div className={styles.miningList}>
        <List
          grid={{
            gutter: 32,
            xs: 1,
            sm: 1,
            md: 2,
            lg: 2,
            xl: 2,
            xxl: 2,
          }}
          dataSource={nftRecords}
          renderItem={(item) => (
            <List.Item>
              <MiningProfileListItem
                record={item}
                claimState={claimState}
                refundState={refundState}
                claimHandler={claimHandler}
                refundHandler={refundHandler}
              />
            </List.Item>
          )}
        />
      </div>
    </div>
  );
};

MiningProfile.propTypes = {};

export default MiningProfile;
