import { useEffect, useReducer } from "react";
import { ethers } from "ethers";

import {
  readContractReducer,
  FETCH_STATE,
  NFT_ATTRITURES,
  fromPattern,
  NFTS,
} from "../utils/utils";
import {
  useManagerContract,
  useMulticallContract,
  useTHContract,
} from "./useContract";
import { useIsMounted } from "./useIsMounted";
import { useUpdate } from "./useUpdate";
import { useWallet } from "./useWallet";
import { multicallHelper } from "../utils/multicall";
import TH_ABI from "../abis/treasureHunter.json";

const NFT_ATTR = Object.values(NFT_ATTRITURES).map((item) =>
  ethers.utils.formatBytes32String(item)
);

const decodeAttrArray = (attr) => {
  // decode attributes
  const decoded = Object.values(NFT_ATTRITURES).reduce(
    (accum, item, index) => ({
      ...accum,
      [item]: attr[index].toNumber(),
    }),
    {}
  );
  // get value and duration
  const valueDuration = fromPattern(decoded[NFT_ATTRITURES.PATTERN]);
  return { ...decoded, ...valueDuration };
};

export const useNFTRecords = () => {
  const [state, dispatch] = useReducer(readContractReducer, {
    isLoading: false,
    isError: false,
    data: [],
  });
  const THContract = useTHContract();
  const multicallContract = useMulticallContract();
  const managerContract = useManagerContract();
  const isMounted = useIsMounted();
  const { updateByTimer } = useUpdate();
  const { account } = useWallet();

  useEffect(() => {
    const fetchData = async () => {
      try {
        if (!(THContract && account)) {
          return;
        }
        dispatch({ type: FETCH_STATE.INIT });

        // get nft balance
        const balance = (await THContract.balanceOf(account)).toNumber();

        // get nft ids
        const idCalls = [...Array(balance).keys()].map((item) => [
          THContract.address,
          "tokenOfOwnerByIndex",
          [account, item],
        ]);
        const idRes = (
          await multicallHelper(multicallContract, TH_ABI.abi, idCalls)
        )
          .flat()
          .map((item) => item.toNumber());

        // get nft attr
        const attrCalls = idRes.map((id) => [
          THContract.address,
          "getUint256Attributes",
          [NFT_ATTR, id],
        ]);
        const attrRes = (
          await multicallHelper(multicallContract, TH_ABI.abi, attrCalls)
        )
          .flat()
          .map((item) => decodeAttrArray(item));
        // get nft skin
        const skin = attrRes.map((attr) => ({
          skin: NFTS.find((item) => item.value === attr.value)?.id,
        }));
        // get earned
        const earned = (await managerContract.earned(idRes)).map((item) => ({
          rewardAmount: Number(ethers.utils.formatEther(item.rewardAmount)),
          rewardUnlockTimestamp: item.unlockTimestamp.toNumber(),
        }));
        // assemble nft record
        const records = idRes.map((id, index) => ({
          id,
          ...attrRes[index],
          ...earned[index],
          ...skin[index],
        }));
        console.log(records);
        if (isMounted.current) {
          dispatch({
            type: FETCH_STATE.SUCCESS,
            payload: records,
          });
        }
      } catch (err) {
        console.error(err);
        if (isMounted.current) {
          dispatch({ type: FETCH_STATE.FAILURE });
        }
      }
    };
    fetchData();
  }, [
    THContract,
    managerContract,
    multicallContract,
    isMounted,
    updateByTimer,
    account,
  ]);

  return state;
};
