import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import classnames from "classnames";
import styles from "./Countdown.module.scss";
import { DAY, HOUR, MINUTES, SECOND, YEAR } from "../../utils/utils";

const Countdown = (props) => {
  const [timeLeft, setTimeLeft] = useState({
    sec: 0,
    min: 0,
    hour: 0,
    day: 0,
  });
  const { timestamp, className, hideDay } = props;

  function calculateTimeLeft(endTS) {
    const timeLeftLocal = {
      year: 0,
      day: 0,
      hour: 0,
      min: 0,
      sec: 0,
    };
    let diff = (+new Date(endTS) - +new Date()) / 1000;
    // clear countdown when date is reached
    if (diff <= 0) return timeLeftLocal;

    // calculate time difference between now and expected date
    if (diff >= YEAR) {
      // 365.25 * 24 * 60 * 60
      timeLeftLocal.year = Math.floor(diff / YEAR);
      diff -= timeLeftLocal.year * YEAR;
    }
    if (diff >= DAY) {
      // 24 * 60 * 60
      timeLeftLocal.day = Math.floor(diff / DAY);
      diff -= timeLeftLocal.day * DAY;
    }
    if (diff >= HOUR) {
      // 60 * 60
      timeLeftLocal.hour = Math.floor(diff / HOUR);
      diff -= timeLeftLocal.hour * HOUR;
    }
    if (diff >= MINUTES) {
      timeLeftLocal.min = Math.floor(diff / MINUTES);
      diff -= timeLeftLocal.min * MINUTES;
    }
    timeLeftLocal.sec = Math.floor(diff / SECOND);

    return timeLeftLocal;
  }

  function addLeadingZeros(val) {
    let value = val;
    value = String(value);
    while (value.length < 2) {
      value = `0${value}`;
    }
    return value;
  }

  useEffect(() => {
    const timer = setTimeout(() => {
      setTimeLeft(calculateTimeLeft(timestamp));
    }, 1000);

    return () => {
      clearTimeout(timer);
    };
  }, [timestamp, timeLeft]);

  return (
    <span className={classnames(styles.Countdown, className)}>
      {parseInt(timeLeft.day, 10) > 0 && !hideDay ? (
        <>
          <span className={styles.countdownCol}>
            {addLeadingZeros(timeLeft.day)}
          </span>
          <span className={styles.colon}>:</span>
        </>
      ) : null}
      <span className={styles.countdownCol}>
        {addLeadingZeros(timeLeft.hour)}
      </span>
      <span className={styles.colon}>:</span>
      <span className={styles.countdownCol}>
        {addLeadingZeros(timeLeft.min)}
      </span>
      <span className={styles.colon}>:</span>
      <span className={styles.countdownCol}>
        {addLeadingZeros(timeLeft.sec)}
      </span>
    </span>
  );
};

Countdown.propTypes = {
  timestamp: PropTypes.number,
  className: PropTypes.string,
  hideDay: PropTypes.bool,
};

Countdown.defaultProps = {
  timestamp: new Date().getTime(),
  className: "",
  hideDay: false,
};

export default Countdown;
