import React, { useEffect, useState } from "react";
import classNames from "classnames";
import { NavLink } from "react-router-dom";
import { Dropdown, Menu } from "antd";
import { MenuOutlined } from "@ant-design/icons";
import { useTranslation } from "react-i18next";
import { useIsMobileView } from "../../hooks/useIsMobileView";
import WalletMenu from "../WalletMenu/WalletMenu";
import styles from "./Header.module.scss";

const LINKS = [
  { text: "About", path: "/about" },
  { text: "Mining", path: "/mining" },
];

const langs = [
  { key: "en", label: "English" },
  { key: "zh", label: "" },
  { key: "ar", label: "" }, // de: { label: "Deutsch", icon: "de" }, // el: { label: "E", icon: "el" },
  { key: "es", label: "Espaol" }, // fi: { label: "Suomi", icon: "fi" },
  { key: "fr", label: "Franais" }, // zh_HANT: { label: '' }, // ja: { label: "", icon: "ja" }, // pt: { label: "Portugus", icon: "pt" }, // it: { label: "Italiano", icon: "it" }, // ro: { label: "Romn", icon: "ro" }, // ko: { label: "", icon: "ko" },
  { key: "ru", label: "" }, // bn: { label: "", icon: "bn" }, // hr: { label: 'hrvatski' },
  { key: "hi", label: "" }, // hu: { label: "Magyar", icon: "hu" }, // vi: { label: "Ting Vit", icon: "vi" },
];

const Header = () => {
  const isMobileView = useIsMobileView();
  const [menuCollapsed, setMenuCollapsed] = useState(isMobileView || false);
  const [currentLang, setCurrentLang] = useState("en");
  const { i18n, t } = useTranslation();

  useEffect(() => {
    if (isMobileView) {
      setMenuCollapsed(true);
    } else {
      setMenuCollapsed(false);
    }
  }, [isMobileView]);

  useEffect(() => {
    setCurrentLang(i18n.resolvedLanguage);
  }, [i18n.resolvedLanguage]);

  const toggleMenu = () => {
    if (isMobileView) {
      setMenuCollapsed(!menuCollapsed);
    }
  };

  const langMenu = (
    <Menu
      items={langs}
      onClick={(item) => {
        i18n.changeLanguage(item.key);
      }}
      className={styles.menu}
    />
  );

  return (
    <div className={styles.Header}>
      <div className={styles.iconWrap}>
        <img src="/static/images/header/logo.png" alt="logo" />
      </div>
      {!menuCollapsed && (
        <div className={styles.navWrap}>
          {Object.values(LINKS).map((item) => (
            <NavLink
              key={`Link${item.text}`}
              to={item.path}
              onClick={toggleMenu}
              className={(isActive) =>
                classNames(styles.navLink, { [styles.active]: isActive })
              }
            >
              {t(item.text)}
            </NavLink>
          ))}
          {isMobileView && (
            <Dropdown overlay={langMenu}>
              <div
                className={styles.mobileLangButton}
                onClick={(e) => e.preventDefault()}
              >
                <img
                  src={`/static/images/header/flags/${currentLang}.svg`}
                  alt={currentLang}
                />
              </div>
            </Dropdown>
          )}
          )
        </div>
      )}
      <div className={styles.walletWrap}>
        {!isMobileView && (
          <Dropdown overlay={langMenu}>
            <div
              className={styles.langButton}
              onClick={(e) => e.preventDefault()}
            >
              <img
                src={`/static/images/header/flags/${currentLang}.svg`}
                alt={currentLang}
              />
            </div>
          </Dropdown>
        )}
        {/* {!account && (
          <img
            src="/static/images/header/wallet.png"
            alt="wallet"
            onClick={() => {
              connect(WALLET.METAMASK);
            }}
          />
        )}
        {account && (
          <Button className={styles.walletButton} onClick={disconnect}>
            {truncAddress(account)}
          </Button>
        )} */}
        <WalletMenu />

        {isMobileView && (
          <MenuOutlined className={styles.menuIcon} onClick={toggleMenu} />
        )}
      </div>
    </div>
  );
};

Header.propTypes = {};

export default Header;
