export const useIsRegistered = (address) => {
  const [state, dispatch] = useReducer(readContractReducer, {
    isLoading: false,
    isError: false,
    data: null,
  });
  const invitationContract = useInvitationContract();
  const isMounted = useIsMounted();
  const { account } = useWallet();
  const { updateByTimer } = useUpdate();

  useEffect(() => {
    const fetchData = async () => {
      try {
        if (
          !(
            invitationContract &&
            ((address && ethers.utils.isAddress(address)) || account)
          )
        ) {
          return;
        }
        dispatch({ type: FETCH_STATE.INIT });
        const data = await invitationContract.isRegistered(address || account);
        if (isMounted.current) {
          dispatch({ type: FETCH_STATE.SUCCESS, payload: data });
        }
      } catch (err) {
        console.error(err);
        if (isMounted.current) {
          dispatch({ type: FETCH_STATE.FAILURE });
        }
      }
    };
    fetchData();
  }, [invitationContract, isMounted, account, address, updateByTimer]);

  return state;
};

export const useInviterRecords = (address) => {
  const [state, dispatch] = useReducer(readContractReducer, {
    isLoading: false,
    isError: false,
    data: {},
  });
  const invitationContract = useInvitationContract();
  const isMounted = useIsMounted();
  const { account } = useWallet();
  const { updateByTimer } = useUpdate();

  useEffect(() => {
    const fetchData = async () => {
      try {
        if (
          !(
            invitationContract &&
            ((address && ethers.utils.isAddress(address)) || account)
          )
        ) {
          return;
        }
        dispatch({ type: FETCH_STATE.INIT });
        const data = await invitationContract.inviterRecords(
          address || account
        );
        if (isMounted.current) {
          dispatch({
            type: FETCH_STATE.SUCCESS,
            payload: decodeInviterRecord(data),
          });
        }
      } catch (err) {
        console.error(err);
        if (isMounted.current) {
          dispatch({ type: FETCH_STATE.FAILURE });
        }
      }
    };
    fetchData();
  }, [invitationContract, isMounted, account, address, updateByTimer]);

  return state;
};

export const useRegisteredInvitationCodeContains = () => {
  const invitationContract = useInvitationContract();
  const isValidInvitation = async (code) => {
    if (!code) {
      return false;
    }
    try {
      const result = await invitationContract.registeredInvitationCodeContains(
        ethers.utils.formatBytes32String(code)
      );
      return result;
    } catch (error) {
      console.log(error);
      return false;
    }
  };

  return { isValidInvitation };
};
