import React from "react";
import PropTypes from "prop-types";
import classNames from "classnames";
import { useTranslation } from "react-i18next";

import Button from "../../UI/Button/Button";
import Countdown from "../../Countdown/Countdown";

import styles from "./MiningProfileListItem.module.scss";
import commonStyles from "../../../common.module.scss";
import { checkTime } from "../../../utils/utils";

const MiningProfileListItem = (props) => {
  const { refundHandler, claimHandler, record, claimState, refundState } =
    props;
  const { t } = useTranslation();

  return (
    <div className={styles.MiningProfileListItem}>
      <img src={`/static/images/nfts/${record.skin}.gif`} alt="" />
      <div className={styles.contentWrap}>
        <div className={styles.tagWrap}>
          <div className={styles.durationTag}>
            <h2 className={commonStyles.gradientTitle}>
              {record.duration} {t("Days")}
            </h2>
          </div>
          <div className={styles.durationTag}>
            <h2 className={commonStyles.gradientTitle}>{record.value} USDT</h2>
          </div>
        </div>
        <div className={styles.valueWrap}>
          <div className={styles.valueItem}>
            <p className={classNames(styles.title, commonStyles.mainText)}>
              {t("Redeem in")}
            </p>
            <Countdown
              timestamp={record.unlockTimestamp * 1000}
              className={styles.value}
            />
          </div>
          <div className={styles.valueItem}>
            <p className={classNames(styles.title, commonStyles.mainText)}>
              {t("Dividend in")}
            </p>
            <Countdown
              timestamp={record.unlockTimestamp * 1000}
              className={styles.value}
              hideDay
            />
          </div>
          <div className={styles.valueItem}>
            <p className={classNames(styles.title, commonStyles.mainText)}>
              {t("Claimable")}
            </p>
            <p className={styles.value}>{record.rewardAmount}</p>
          </div>
        </div>
        <div className={styles.buttonWrap}>
          <Button
            onClick={() => {
              refundHandler([record.id]);
            }}
            className={styles.button}
            secondary
            loading={refundState.isLoading}
            disabled={!checkTime(record.unlockTimestamp * 1000)}
          >
            {t("Redeem")}
          </Button>
          <Button
            onClick={() => {
              claimHandler([record.id]);
            }}
            className={styles.button}
            gradient
            loading={claimState.isLoading}
            disabled={record.rewardAmount === 0}
          >
            {t("Claim")}
          </Button>
        </div>
      </div>
    </div>
  );
};
MiningProfileListItem.propTypes = {
  refundHandler: PropTypes.func.isRequired,
  claimHandler: PropTypes.func.isRequired,
  record: PropTypes.objectOf(PropTypes.any).isRequired,
  claimState: PropTypes.objectOf(PropTypes.any).isRequired,
  refundState: PropTypes.objectOf(PropTypes.any).isRequired,
};

MiningProfileListItem.defaultProps = {};

export default MiningProfileListItem;
