import { useEffect, useReducer } from "react";
import { ethers } from "ethers";
import { message } from "antd";

import { readContractReducer, FETCH_STATE } from "../utils/utils";
import { useManagerContract } from "./useContract";
import { useIsMounted } from "./useIsMounted";
import { useWallet } from "./useWallet";
import { useUpdate } from "./useUpdate";

const decodeRecord = (record) => ({
  bonusAmount: Number(ethers.utils.formatEther(record.bonusAmount)),
  inviteeCapitalAmounts: record.inviteeCapitalAmounts.map((item) =>
    Number(ethers.utils.formatEther(item))
  ),
});

const decodeRank = (record) =>
  record.map((item) => ({
    who: item.who,
    firstLayerInviteeCapitalAmount: Number(
      ethers.utils.formatEther(item.firstLayerInviteeCapitalAmount)
    ),
  }));

export const useRecord = (address) => {
  const [state, dispatch] = useReducer(readContractReducer, {
    isLoading: false,
    isError: false,
    data: null,
  });
  const managerContract = useManagerContract();
  const isMounted = useIsMounted();
  const { account } = useWallet();
  const { updateByTimer } = useUpdate();

  useEffect(() => {
    const fetchData = async () => {
      try {
        if (
          !(
            managerContract &&
            ((address && ethers.utils.isAddress(address)) || account)
          )
        ) {
          return;
        }
        dispatch({ type: FETCH_STATE.INIT });
        const data = await managerContract.records(address || account);
        if (isMounted.current) {
          dispatch({ type: FETCH_STATE.SUCCESS, payload: decodeRecord(data) });
        }
      } catch (err) {
        console.error(err);
        if (isMounted.current) {
          dispatch({ type: FETCH_STATE.FAILURE });
        }
      }
    };
    fetchData();
  }, [managerContract, isMounted, account, address, updateByTimer]);

  return state;
};

export const useRanks = () => {
  const [state, dispatch] = useReducer(readContractReducer, {
    isLoading: false,
    isError: false,
    data: [],
  });
  const managerContract = useManagerContract();
  const isMounted = useIsMounted();
  const { updateByTimer } = useUpdate();

  useEffect(() => {
    const fetchData = async () => {
      try {
        if (!managerContract) {
          return;
        }
        dispatch({ type: FETCH_STATE.INIT });
        const data = await managerContract.ranks();
        if (isMounted.current) {
          dispatch({ type: FETCH_STATE.SUCCESS, payload: decodeRank(data) });
        }
      } catch (err) {
        console.error(err);
        if (isMounted.current) {
          dispatch({ type: FETCH_STATE.FAILURE });
        }
      }
    };
    fetchData();
  }, [managerContract, isMounted, updateByTimer]);

  return state;
};

export const useBuy = () => {
  const [state, dispatch] = useReducer(readContractReducer, {
    isLoading: false,
    isError: false,
    data: null,
  });
  const managerContract = useManagerContract();
  const isMounted = useIsMounted();

  const buy = async (patterns, inviterCode) => {
    console.log("buy", patterns, inviterCode);
    try {
      const { utils } = ethers;
      if (!(managerContract && patterns?.length)) {
        return;
      }
      dispatch({ type: FETCH_STATE.INIT });
      const tx = await managerContract.buy(
        patterns,
        utils.formatBytes32String(inviterCode)
      );
      message.info("Request sent");
      console.log("tx", tx);
      const receipt = await tx.wait();
      message.success("Transaction completed");
      console.log("receipt", receipt);
      if (isMounted.current) {
        dispatch({ type: FETCH_STATE.SUCCESS, payload: null });
      }
    } catch (error) {
      console.log(error);
      message.error("Transaction failed");
      if (isMounted.current) {
        dispatch({ type: FETCH_STATE.FAILURE });
      }
    }
  };

  return { state, buy };
};

